import React, { Component } from 'react';
import {getComicsView,getMarvelCharacters,checkCharacterAvailability,createCharacter,getMarvelCharactersComics} from '../util/APIUtils';
import { withRouter } from 'react-router-dom';
import LoadingIndicator  from '../common/LoadingIndicator';
import {Carousel,notification, Pagination, Layout, Modal } from 'antd';
import './Show.css';
import NotFound from '../common/NotFound';
import ServerError from '../common/ServerError';
import Characters from './Characters';
import Comics from './Comics';
import Favourite from './Favourite';


const { Header, Sider, Content,
        } = Layout;



class Show extends Component {
    
    constructor(props) {
        super(props);
        this.state = {
            characters: [],
            page: 0,
            size: 20,
            totalElements: 0,
            results:[],
            currenUser:[],
            isLoading: false,
            comics:[],
            visible:false,
            charactersid:null,
            viewcomics:[],
            comicssize:0,
            lengthcomics:0,
            isload:false,

            
        };
        this.loadCharacte = this.loadCharacte.bind(this);
        this.onShowSizeChange=this.onShowSizeChange.bind(this);
        this.saveData=this.saveData.bind(this);
        this.showModal=this.showModal.bind(this);
        this.loadComicsView=this.loadComicsView.bind(this);
        this.funtionRandom=this.funtionRandom.bind(this);

    }

    funtionRandom(item){
        if (this.props.isAuthenticated===false) {
             notification.error({
                            message: "Favourite",
                            description: 'Sorry! login no authenticate',
                        });
            return;
        }
        getMarvelCharactersComics(item.id).then(response => {
            let rand;
            for (var i = 0; i < 3; i++) {
                rand=Math.floor(Math.random() * response.data.results.length) + 1;
                   console.log(rand-1);
                  if (response.data.results.length===0) {
                    notification.error({
                            message: "No Pictures",
                            description: 'Sorry! no resource',
                        });
                    return;
                  }
                   this.saveData(response.data.results[rand-1],item)
               }   
        });


    }

    showModal(item){

        this.setState({
        visible: true, isload:true });
        getMarvelCharactersComics(item.id).then(response => {
           if (response.data.results.length===0) {
            this.hideModal();
            notification.error({
                            message: "Resource",
                            description: 'Sorry! no pictures',
                        });
           }
           this.setState({
              comics:response.data.results,
              charactersid:item
            });
        });


        
    }

     hideModal = () => {
        this.setState({
          visible: false,
          comics:[],
          charactersid:null,
        });
    }

   
    saveData(item,id){

        if (this.props.isAuthenticated===false) {
             notification.error({
                            message: "favourite",
                            description: 'Sorry! login no authenticate',
                        });
            return;
        }

        
            const comicsRequest = {
                charactersid: id.id,
                userid: this.props.currentUser.id,
                comicsid: item.id,
                uriimage: item.thumbnail.path+"."+item.thumbnail.extension,
                description: item.description,
                titleCharacter: id.name,
                titleComics: "."+item.title
                
            };
            



            checkCharacterAvailability(id.id,item.id,this.props.currentUser.id)
            .then(response => {
                if(response.available) {
                
                    createCharacter(comicsRequest)
                    .then(response => {
                        notification.success({
                            message: item.name+" favourite",
                            description: "Thank you! The favorite comics was saved successfully",
                        });
                        this.loadComicsView(0,4);          
                        
                    }).catch(error => {
                        notification.error({
                            message: item.name+" favourite",
                            description: error.message || 'Sorry! Something went wrong. Please try again!'
                        });
                    });


                    


                } else {
                notification.error({
                            message: "favourite",
                            description: 'Sorry! Character already exists!',
                        });
                }
            }).catch(error => {
            
            });
            
            
        
    }

    loadComicsView(page,size){
        if (page>0) {
            page=page-1;
        }
        getComicsView(page,size).then(item=>{
            this.setState({
                viewcomics:item.content,
                comicssize:item.totalElements

            });
        });       
        
    }

     loadCharacte(current) {

        let promise;
        if (current) {
            promise = getMarvelCharacters(current);    
        }else{
            promise = getMarvelCharacters(0);    
        }
        
        
        if(!promise) {
            return;
        }

        this.setState({
            isLoading: true
        });

        promise            
        .then(response => {
            const characters = this.state.characters.slice();
            this.setState({
                characters: characters.concat(response.data),
                size:response.data.limit,
                page:response.data.offset,
                totalElements:response.data.total,
                results:response.data.results, 
                isLoading: false

                
            })

        }).catch(error => {
            if(error.status === 404) {
                this.setState({
                    notFound: true,
                    isLoading: false
                });
            } else {
                this.setState({
                    serverError: true,
                    isLoading: false
                });        
            }
        }); 
        
        
    }

    componentDidMount() {
            this.loadCharacte(this.state.current);
            this.loadComicsView(0,4);


    }

    componentDidUpdate(nextProps) {
        if(this.props.isAuthenticated !== nextProps.isAuthenticated) {
            
            this.setState({
            characters: [],
            page: 0,
            size: 20,
            totalElements: 0,
            results:[],
            isLoading: false
            });    
            
        }
    }

    onShowSizeChange(page) {
      this.loadCharacte(page);
    }
    
    render() {
        
        if(this.state.notFound) {
            return <NotFound />;
        }

        if(this.state.serverError) {
            return <ServerError />;
        }

        if (this.props.isAuthenticated===false) {
            
        }
       
        const favouriteViews=<Favourite element={this.state.comicssize} searcomics={this.loadComicsView.bind(this)} results={this.state.viewcomics} />;
         

        let comicsView=[];
        this.state.comics.forEach(response=>{
            comicsView.push(<Comics  charactersid={this.state.charactersid} savedata={this.saveData.bind(this)} results={response} />);
        });
        const characterViews = <Characters random={this.funtionRandom.bind(this)} results={this.state.results} callback={this.showModal.bind(this)} />;
        

        

        return(
            <Layout>
                
                  <Header>
                    <h2>Favourite Comics</h2>
                  </Header>
                    {
                        this.state.isLoading ? 
                        <LoadingIndicator />:
                        <Layout>

                        <Sider>
                            
                            {favouriteViews}
                        </Sider>
                           <Content> 
                            <Pagination onChange={(page)=>this.onShowSizeChange(page)} showQuickJumper current={this.state.page}  total={this.state.totalElements*10} />
                            <br></br>
                            {characterViews}
                            <Pagination onChange={(page)=>this.onShowSizeChange(page)} showQuickJumper current={this.state.page}  total={this.state.totalElements*10} />
                            </Content>
                        </Layout>
                    }
            <div>
         
                <Modal

                bodyStyle={{background:'#330000'}}
                    style={{background:'#330000'}}
                  width={700}
                  visible={this.state.visible}
                  onOk={this.hideModal}
                  onCancel={this.hideModal}
                  okText="Ok"
                  cancelText="cancel">
                    {this.state.comics.length===0 ? 
                        <LoadingIndicator />:
                        <div><Carousel autoplay={true} effect="fade">
                            {comicsView}
                          </Carousel>
                        </div>
                    }
                </Modal>
             </div>
            </Layout>
            

        );
    }


}
export default withRouter(Show);