import React, { Component } from 'react';
import { Avatar, Col, Row ,Card, Tag,Popconfirm, message } from 'antd';






class Comics extends Component {


  calc(item,characters){
            this.props.savedata(item,characters);
        }

  confirm(e,c) {
    console.log(e);
   this.calc(e,c);
    message.success('Click on Yes');
  }

   cancel(e) {
    console.log(e);
    message.error('Click on No');
  }
        




	    render() {
        
        return (
      
          <div>

          <Row align="center">
            <Col span={12}>
              <div>
              <Card bordered={false} style={{background:'#330000'}}>

              <Avatar shape="square" size={250} src={this.props.results.thumbnail.path+"."+this.props.results.thumbnail.extension} />
               <Popconfirm title="Are you sure add favourite?" onConfirm={() => this.confirm(this.props.results, this.props.charactersid)} onCancel={() => this.cancel('cancel')} okText="Yes" cancelText="No">
                           <Tag style={{'font-size': '15px'}} color="#f50">Add Favourite</Tag>
                          </Popconfirm>

               <p style={{color:'#FFFFFF'}}>{this.props.results.title}</p>
               </Card></div>
              </Col>
            <Col span={12}><div>
                <Card style={{opacity: 0.5, background:'#000000'}}>
                    { this.props.results.description ? 
                        <div>
                          <Tag color="red">Description</Tag>
                          <p style={{color:'#FFFFFF', 'font-size': '11px'}} >{this.props.results.description}</p>
                        </div> : <h3 style={{color:'#FFFFFF'}}>No description</h3>
                    }
                </Card>
                </div>
              
            </Col>

          </Row>
          
          </div>
        

        );
    }
}

export default Comics;
