import React, { Component } from 'react';
import { Button, List, Card, Avatar,Divider, Tag, Popconfirm,message, Icon  } from 'antd';
const { Meta } = Card;



class Characters extends Component {
        calc(item){
            this.props.callback(item);
        }
        calc2(item){
          this.props.random(item);
        }

        confirm(e) {
          console.log(e);

          message.success('Click on Yes');
          this.calc2(e)
        }

        cancel(e) {
          console.log(e);
          message.error('Click on No');
        }

       


	    render() {
        
              
        return (
           <List
            grid={{ gutter: 20, column: 2 }}
            dataSource={this.props.results}
            renderItem={item => (
                
              <List.Item>
                <Card >
                  <Meta
                    avatar={<div><Divider type="vertical" /><Avatar size={120} src={item.thumbnail.path+"."+item.thumbnail.extension} /></div>}
                    title={ <Tag color="purple">{item.name}</Tag>}
                    description={<Divider><Button type="primary" shape="circle" size="large" onClick={() => this.calc(item)} icon="picture"></Button></Divider>}
                  />
                  
                  {<Popconfirm title="Are you add random comics?" onConfirm={() => this.confirm(item)} onCancel={() => this.cancel('cancel')} okText="Yes" cancelText="No">
                      
                      <a>selected 3 comics <Icon type="smile" style={{ fontSize: '20px', color: '#08c' }} theme="outlined" /></a>
                      
                    </Popconfirm>}
                </Card>
              </List.Item>
            )}
          />
        );
    }

}



export default Characters;
