import { API_BASE_URL, ACCESS_TOKEN , API_MARVEL_URL, PUBLICKEY, PRIVATEKEY, TS, FAVOURITE_LIST_SIZE} from '../constants';

var MD5=require('md5');

const request = (options) => {
    const headers = new Headers({
        'Content-Type': 'application/json',
    })
    
    if(localStorage.getItem(ACCESS_TOKEN)) {
        headers.append('Authorization', 'Bearer ' + localStorage.getItem(ACCESS_TOKEN))
    }

    const defaults = {headers: headers};
    options = Object.assign({}, defaults, options);

    return fetch(options.url, options)
    .then(response => 
        response.json().then(json => {
            if(!response.ok) {
                return Promise.reject(json);
            }
            return json;
        })
    );
};

const requestMarvel = (options) => {
    const headers = new Headers({
        'Content-Type': 'application/json',
    })
    
    const defaults = {headers: headers};
    options = Object.assign({}, defaults, options);

    return fetch(options.url, options)
    .then(response => 
        response.json().then(json => {
            if(!response.ok) {
                return Promise.reject(json);
            }
            return json;
        })
    );
};





export function login(loginRequest) {
    return request({
        url: API_BASE_URL + "/auth/signin",
        method: 'POST',
        body: JSON.stringify(loginRequest)
    });
}

export function signup(signupRequest) {
    return request({
        url: API_BASE_URL + "/auth/signup",
        method: 'POST',
        body: JSON.stringify(signupRequest)
    });
}

export function createCharacter(charactersRequest) {
    return request({
        url: API_BASE_URL + "/marvel",
        method: 'POST',
        body: JSON.stringify(charactersRequest)
    });
}

export function checkUsernameAvailability(username) {
    return request({
        url: API_BASE_URL + "/user/checkUsernameAvailability?username=" + username,
        method: 'GET'
    });
}

export function checkEmailAvailability(email) {
    return request({
        url: API_BASE_URL + "/user/checkEmailAvailability?email=" + email,
        method: 'GET'
    });
}

export function checkCharacterAvailability(characters,comics,userid) {
    return request({
        url: API_BASE_URL + "/user/checkComicsAvailability?characters=" + characters
        +"&comics="+ comics + "&userid=" + userid,

        method: 'GET'
    });
}

export function getAllPolls(page, size, characters) {
    page = page || 0;
    size = size || FAVOURITE_LIST_SIZE;

    return request({
        url: API_BASE_URL + "/marvel?page=" + page + "&size=" + size + "&characters" + characters,
        method: 'GET'
    });
}



export function getCurrentUser() {
    if(!localStorage.getItem(ACCESS_TOKEN)) {
        return Promise.reject("No access token set.");
    }

    return request({
        url: API_BASE_URL + "/user/me",
        method: 'GET'
    });
}

export function getUserProfile(username) {
    return request({
        url: API_BASE_URL + "/users/" + username,
        method: 'GET'
    });
}

export function getComicsView(page,size) {
     page = page || 0;
    size = size || FAVOURITE_LIST_SIZE;
    return request({
        url: API_BASE_URL + "/marvel?page="+page+"&size="+size,
        method: 'GET'
    });
}


export function getMarvelCharacters(offset) {
    return requestMarvel({
        url: API_MARVEL_URL + "/characters?ts="+TS+"&apikey="+PUBLICKEY+"&hash="+MD5(TS+PRIVATEKEY+PUBLICKEY)+"&offset="+offset,
        method: 'GET'    
    });
}

export function getMarvelCharactersComics(charactersid) {
    return requestMarvel({
        url: API_MARVEL_URL + "/characters/"+charactersid+"/comics?ts="+TS+"&apikey="+PUBLICKEY+"&hash="+MD5(TS+PRIVATEKEY+PUBLICKEY),
        method: 'GET'    
    });
}





