import React, { Component } from 'react';
import { List, Avatar, Card, Tag } from 'antd';
const { Meta } = Card;


class Favoutire extends Component {
        calc(page,size){
            this.props.searcomics(page,size);

        }



       
      

	    render() {
        
        const sizecomics=this.props.element;

        return (

          <List
            itemLayout="vertical"
            size="large"
            pagination={{
              onChange: (page) => {
                this.calc(page,4);

              },
              pageSize: 4,
              total:sizecomics,
            }}
            dataSource={this.props.results}
            
            renderItem={item => (
              <List.Item>
              
              <Card align="center" bordered={false}>
              <p style={{'font-size': '10px'}}><Tag color="green"><b>Character:</b></Tag>{  item.titleCharacter}</p>
              <p style={{'font-size': '10px'}}><b>Comics:</b>{ item.titleComics}</p>
              
                <Meta
                    avatar={<Avatar shape="square" size={140} src={item.uriimage} />}
                    
                    
                  />
                 
                </Card >
              </List.Item>
            )}
          />
           
        );
    }

}



export default Favoutire;
